import * as React from 'react';

const NPCSheetAttributes = (props: any) => {
    return (
        <div className={"npcsheet-attributes"}>
            <p>Armor Class {props.ac}</p>
            <p>Hit Points {props.hp}</p>
            <p>Speed {props.speed}</p>
        </div>
    );
};

export default NPCSheetAttributes