import * as React from 'react';
import {
    useNavigate,
} from 'react-router-dom';

import Button from '../Button';
import '../../scss/MyNPCButton.css';

const MyNPCHeader = () => {
    const navigate = useNavigate()

    const onClick = (e: any) => {
        e.preventDefault();
        navigate(`/npc/builder`);
    };

    return (
        <header>
            <div>
                <Button className="mynpc-button" onClick={onClick}>CREATE AN NPC</Button>
            </div>
            <div>
                <h1>My Characters</h1>
                <hr></hr>
            </div>
        </header>
    );
}

export default MyNPCHeader;