import { listenerCount } from 'process';
import * as React from 'react';

import Button from '../Button';
import '../../scss/CharacterCard.css';

import ListingContainer from '../containers/ListingContainer';


const CharacterCardActions = (props: any) => {
    return (
            <ListingContainer>
                <Button className="charactercard-button">VIEW</Button>
                <Button className="charactercard-button">EDIT</Button>
                <Button className="charactercard-button">COPY</Button>
                <Button className="charactercard-delete-button">DELETE</Button>
            </ListingContainer>
    );
}

export default CharacterCardActions;