import * as React from 'react';

import ListingContainer from '../containers/ListingContainer';
import Container from '../containers/Container';

import CharacterCard from './CharacterCard';
import MyNPCCollection from './MyNPCCollection';
import MyNPCHeader from './MyNPCHeader';

const MyNPC = () => {
    return (
        <Container>
            <div>
                <MyNPCHeader />
                <MyNPCCollection limit="0" />
                <ListingContainer>
                    <CharacterCard characterName="Test1" cr="1" characterType="Humanoid" class="Wizard"/>
                    <CharacterCard characterName="Test2" cr="4" characterType="Humanoid" class="Warlock"/>
                    <CharacterCard characterName="Test3" cr="20" characterType="Humanoid" class="Cleric"/>
                </ListingContainer>
            </div>
        </Container>
        
    );
}

export default MyNPC;