import * as React from 'react';

import '../../scss/NPCSheet.css';

const NPCItem = (props: any) => {

    if (props.items === undefined)
        return(null)

    return (
        <p>
            <strong className={"npctidbit-type"}>{props.item}</strong>{props.items}
        </p>
    );
};

export default NPCItem;